/******************************************************************/
/***************************** Global *****************************/
/******************************************************************/ 

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
  width: 100vw;
  overflow-x: hidden;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: black;
}

input:focus {
  outline: none;
}

input::focus-visible {
  outline: none;
}

input:active, button:active {
  outline: none;
}

em {
  font-size: 13px !important;
}

/******************************************************************/
/**************************** Header ******************************/
/******************************************************************/ 

header {
  width: 100%;
  display: block;
  background: transparent;
  padding: 0 5%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  transition: all 0.5s;
  opacity: 1;
}

header.under-element {
  opacity: 0;
  z-index: -1;
}

header.dark {
  background-color: #232529;
  padding-top: 6px;
   padding-bottom: 10px;
}


.topbar {
  display: flex;
  justify-content: flex-end;
  border-bottom: 1px solid rgba(255,255,255,.2);
}

.topbar.hide {
  border-bottom: none;
}

.topnav {
  width: 100%;
  padding-top: 0;
}

.topnav .social-media-container .social-icon {
  margin-right: 10px;
  color: #ff9155;
}

.topbar.hide .topnav {
  padding-top: 0;
}

.topnav>ul {
  display: flex;
  justify-content: space-between;
  height: 40px;
  overflow: hidden;
  align-items: center;
  transition: all 0.5s;
}

.topbar.hide .topnav>ul {
  height: 0;
}

.topbar ul li {
  display: inline-block;
  border-left: 1px solid rgba(255,255,255,.2);
  padding: 5px 15px;
}

.topbar ul li svg {
  width: 24px;
  height: 24px;
  color: #ff9155;
}

.topbar ul li img {
  margin-top: 2px;
  width: 17px;
  height: 17px;
  border-radius: 50px;
  display: block;
}

.topbar ul li span {
  font-size: 0.7rem;
  color: #ff9155;
}

.input-container {
  height: 100%;
  width: 200px;
  padding: 5px 0;
}

.search {
  border-left: none !important;
}

.search-input {
  border-radius: 10px;
  background-color: rgba(255,255,255,0.5);
  border: none;
  width: 100%;
  padding: 4px 15px;
  font-weight: 600;
  font-size: 15px;
  transition: all 0.5s;
}

.search-input:focus {
  background-color: rgba(255,255,255,0.9);
  outline: none;
}

.search-input::focus-visible {
  outline: none;
}

.cart, .account {
  position: relative;
}

.setting {
  display: flex !important;
  justify-content: center;
  align-items: center;
}

.setting a {
  color: #ff9155;
  margin-right: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.setting span {
  line-height: 25px;
  margin-left: 5px;
}

.items-number {
  padding: 5px;
  border-radius: 50%;
  background-color: #CC0000;
  color: #fff !important;
  font-size: 9px !important;
  position: absolute;
  right: 5%;
  top: -3px;
  font-weight: bold;
  text-align: center;
}

.account-status {
  width: 15px !important;
  height: 15px !important;
  color: #fff !important;
  position: absolute;
  top: -1px;
  right: 5px;
}

.navigation {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  transition: all 0.3s;
}

.logo-container{
  display: flex;
  align-items: center;
}

.logo-container>img {
  width: 50px;
  height: 50px;
  margin-right: 10px;
  border-radius: 50%;
}

.logo-container>h1 {
  color: white;
  font-size: 1.5rem;
  margin-bottom: 5px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.logo-container>h1>span {
  font-size: 0.9rem;
  display: block;
  font-style: italic;
}

.menu-container ul li {
  display: inline-block;
  padding: 0 10px;
  color: white;
}

.menu-container ul li a {
  color: white;
  font-weight: 600;
  transition: all 0.5s;
}

.menu-container ul li a:hover {
  color: #ff9155;
}

.mobile-menu {
  display: none;
}

main {
  min-height: 100vh;
}

/******************************************************************/
/******************* Home Page - Intro Block **********************/
/******************************************************************/ 

main {
  position: relative;
  width: 100vw;
  min-height: 100vh;
}

main > img {
  display: block;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
}

main > .overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.2);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro-container {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 200px;
}

.intro-container .intro-content {
  width: 100%;
  height: 100%;
  padding: 150px 10%;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.intro-container .intro-content > img {
  width: 150px;
  height: auto;
}

.intro-container .intro-content > h2 {
  margin: 20px auto 15px;
  color: #fff;
  font-size: 2.5rem;
}

.intro-container .intro-content > p {
  margin: 0 auto 20px;
  color: #999;
}

.intro-container .intro-actions {
  padding-top: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
}

.intro-container .intro-actions  button {
  width: 200px;
  border: 1px solid #fff;
  border-radius: 10px;
  padding: 10px 20px;
  color: white;
  background-color: #21172488;
  font-size: 1rem;
  transition: all 0.5s;
  cursor: pointer;
  margin: 10px;
}

.intro-container .intro-actions button:hover, .intro-container .intro-actions button.active {
  border: 1px solid #ff9155;
  background-color: #ff9155;
}

.grid-paper {
  padding: 20px;
  background-color: #21172488 !important;
  color: #fff !important;
  margin-bottom: 25px;
  box-shadow: 0 5px 15px rgb(0 0 0 / 20%) !important;
  border-radius: 20px !important;
}


.ranks-container {
  width: 100%;
}

.ranks-container .ranks-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ranks-container .ranks-header div {
  width: 75%;
  margin-right: 5%;
}

.ranks-container .ranks-header img {
  width: 100%;

}

.selected-rank-container {
  width: 20% !important;
}

.selected-rank {
  display: block;
  font-weight: bold;
  font-size: 0.9rem;
  color: #fff;
}

.ranks-container .ranks-header div h3 {
  font-size: 2.2rem;
  margin-bottom: 20px;
  text-align: left;
}

.ranks-container .ranks-header div p {
  font-size: 0.9rem;
  text-align: left;
  margin-bottom: 40px;
}

.ranks {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.ranks .rank {
  width: 11.7%;
  padding: 5px;
  margin-right: 10px;
  border-radius: 10px;
  border: 1px solid #eee;
  transition: all 0.5s;
  background-color: transparent;
  cursor: pointer;
  box-shadow: inset 60px 0 120px rgb(119 134 131 / 20%), 0 0 9px 0 rgb(0 0 0 / 60%);
}

.ranks.placement {
  width: 80%;
}

.ranks.placement .rank {
  margin-bottom: 10px;
  width: 17%;
}

.ranks .rank:hover, .ranks .rank.active {
  background-color: #ff9155;
}

.ranks .rank img {
  width: 100%;
}

.divisions-container {
  display: flex;
  align-items: center;
  padding-right: 20px;
}

.divisions {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  width: 55%;
  position: relative;
}

.divisions .division {
  width: 25%;
  padding: 10px 8px;
  margin-right: 10px;
  border-radius: 10px;
  border: 1px solid #eee;
  transition: all 0.5s;
  background-color: transparent;
  cursor: pointer;
  text-align: center;
  box-shadow: inset 60px 0 120px rgb(119 134 131 / 20%), 0 0 9px 0 rgb(0 0 0 / 60%);
}

.divisions .division:hover, .divisions .division.active {
  background-color: #ff9155;
}

.divisions .division strong {
  font-size: 1.5rem;
  font-family: monospace;
}

.myselect {
  width: 45%;
  padding: 10px 14px;
  border-radius: 10px;
  background-color: hsla(0,0%,100%,0.1);
  box-shadow: 0 10px 8px -5px rgb(0 0 0 / 20%);
  color: #fff;
  transition: all 0.5s;
  cursor: pointer;
  line-height: 20px;
}

.myselect option {
  padding: 5px 15px;
  font-size: 15px;
  line-height: 20px;
}

.divisions>input[type=number] {
  width: 90%;
  padding: 10px 14px 10px 120px;
  border-radius: 10px;
  background-color: hsla(0,0%,100%,0.1);
  box-shadow: 0 10px 8px -5px rgb(0 0 0 / 20%);
  color: #fff;
  transition: all 0.5s;
  cursor: pointer;
  position: relative;
}

.divisions>.attached-label {
  display: block;
  position: absolute;
  color: #ddd;
  font-size: 0.9rem;
  left: 20px;
  top: 11px;
}

.myselect:focus {
  outline: none;
  border: 1px solid #ff9155;
  background: #281b2b;
  color: #fff;
}

.ranks-container .options {
  text-align: left;
}

.checkout-price {
  text-align: center;
  font-size: 2.2rem;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  margin: 40px auto;
}

.options-submit button {
  display: block;
  width: 80%;
  margin: 0 auto 10px;
  padding: 10px 20px;
  text-align: center;
  background-color: transparent;
  border: 1px solid #ff9155;
  transition: all 0.5s;
  color: #fff;
  cursor: pointer;
  font-size: 1.2rem;
  border-radius: 10px;
  box-shadow: 0 10px 8px -5px rgb(0 0 0 / 20%);
}

.options-submit button:hover {
  background-color: #ff9155;
}

.switch-container {
  margin-bottom: 5px;
}

.switch-label {
  font-size: 17px;
}

.switch-label > span {
  font-size: 12px;
  margin-right: 5px;
  font-weight: bold;
  color: #ff9155;
}

.switch-label svg {
  color: #ff9155;
  font-size: 15px;
  margin-right: 10px;
}


/******************************************************************/
/**************************** Register ****************************/
/******************************************************************/ 

.register-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  padding-top: 80px;
}

.register-content {
  width: 40%;
  padding: 40px;
  background-color: #21172488 !important;
  color: #fff !important;
  margin-bottom: 25px;
  box-shadow: 0 5px 15px rgb(0 0 0 / 20%) !important;
  border-radius: 20px !important;
}

.register-content h3 {
  color: #fff;
  font-size: 2.2rem;
  font-family: Georgia, 'Times New Roman', Times, serif;
  margin-bottom: 40px;
  text-align: center;
}

.register-content input {
  width: 100%;
  background: hsla(0,0%,100%,0.1);
  color: #fff;
  padding: 15px;
  box-shadow: 0 5px 8px 0 rgb(0 0 0 / 42%);
  box-sizing: border-box;
  border-radius: 7px;
  border: 1px solid transparent;
  outline: 0;
  transition-duration: .4s;
  font-weight: 400;
  font-size: 14px;
  margin-bottom: 20px;
}

.register-content button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin: 20px auto 0;
  padding: 10px 20px;
  text-align: center;
  background-color: transparent;
  border: 1px solid #ff9155;
  transition: all 0.5s;
  color: #fff;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 10px;
  box-shadow: 0 10px 8px -5px rgb(0 0 0 / 20%);
}

.register-content button svg {
  color: #fff;
  margin-right: 15px;
}

.register-content button:hover {
  background-color: #ff9155;
}

/******************************************************************/
/************************** checkout modal ************************/
/******************************************************************/

.order-modal-content {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  max-height: 100%;
  overflow-y: auto;
}

.order-modal-content .modal-col {
  width: 49%;
}

.order-details {
  margin-bottom: 83px;
}

.order-block-content input, .order-block-content select {
  display: block;
  width: 100%;
  border-radius: 10px;
  padding: 8px 15px;
  border: none;
  margin-bottom: 10px;
}

.order-block {
  text-align: left;
}

.order-block-title {
  color: #fff;
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 20px;
}

.order-block-content {
  padding: 20px;
  border: 1px dashed #eee;
  border-radius: 20px;
  margin-bottom: 20px;
}

.order-block-content .flex {
  display: flex;
  align-items: center;
}

.order-block-content .flex.between {
  justify-content: space-between;
}

.order-block-content .flex.between input {
  width: 48%;
}

.order-block-content .flex.start {
  margin-bottom: 15px;
}

.order-block-content .flex.start img {
  margin-right: 15px;
}

.order-block-content .flex.center {
  justify-content: center;
}

.order-block-content .flex h3 {
  font-size: 1.7rem;
  margin-bottom: 10px;
}

.order-block-content .user-infos {
  display: flex;
  justify-content: space-between;
}

.order-block-content .user-infos input {
  width: 31%;
}

.order-block-content .form-infos {
  font-size: 14px; 
  line-height: 1.2;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.order-block-content .form-infos svg {
  font-size: 18px; 
  line-height: 1.2;
  margin-right: 10px;
}

.order-block-content h5 {
  margin-bottom: 15px;
}

.mycheckbox span:last-of-type {
  font-size: 12px;
}

.paypal-container {
  height: 330px;
  overflow-y: auto;
}

.order-submit {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.order-block-content .order-submit .price {
  margin-left: 20px;
  font-size: 2rem;
  font-family: Verdana, sans-serif;
}

.order-block-content .order-submit button {
  width: 200px;
  border: 1px solid #fff;
  border-radius: 10px;
  padding: 10px 20px;
  color: white;
  background-color: #21172488;
  font-size: 1rem;
  transition: all 0.5s;
  cursor: pointer;
  margin: 10px;
}

.order-block-content .order-submit button:hover {
  border: 1px solid #ff9155;
  background-color: #ff9155;
}

.myorders-infos {
  padding: 20px;
}

.myorders-infos>h4 {
  font-size: 1.3rem;
  text-align: center;
  margin-bottom: 15px;
}
.myorders-infos .flex {
  display: flex;
  align-items: center;
}

.myorders-infos .flex.between {
  justify-content: space-between;
}

.bordred-container {
  margin: 20px 0;
  padding: 15px;
  border: 1px dashed #eee;
  border-radius: 10px;
}

.default-display {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.default-display h4 {
  font-size: 1.8rem;
}

.myorders-rank {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.myorders-rank {
 text-align: center;
}

.myorders-rank h3 span {
  text-transform: capitalize;
  margin-right: 5px;
}

.myorders-rank img {
  width: 70px;
}

.mini-text {
  font-size: 12px;
}

.account-informations {
  display: flex;
  justify-content: center;
  align-items: center;
}

.account-informations>div {
  flex: 1;
  margin: 0 2px;
  border-radius: 10px;
  padding: 5px;
  border: 1px dashed #eee;
  text-align: center;
}

.account-informations>div span {
  font-size: 9px;
}

.account-informations>div p {
  margin: 10px auto;
  font-size: 14px;
  font-family: 'Times New Roman', Times, serif;
}


/******************************************************************/
/****************************** Footer ****************************/
/******************************************************************/

footer {
  margin: 0;
  padding: 40px 5%;
  background-color: #000;
}

footer > p {
  color: #fff;
  font-size: 15px;
  line-height: 1.5;
  margin-bottom: 0;
}

.visit-showroom {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 1px solid #eee;
}

.showroom-infos {
  flex: 2;
}

.showroom-infos h4 {
  font-size: 2.2rem;
  margin-bottom: 15px;
  color: #fb0;
  text-shadow: 1px 1px 1px rgb(0 0 0 / 50%);
}

.showroom-infos address {
  margin-bottom: 10px;
}

.getdirection {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.getdirection button {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
  font-size: 1rem;
  text-align: center;
  padding: 12px 25px;
  display: block;
  margin: 0 auto 15px;
  border-radius: 5px;
  transition: all 0.5s;
  cursor: pointer;
  margin-top: 10px;
}

.getdirection button:hover {
  background: #fff;
}

.getdirection button:hover a {
  color: #444;
}

.getdirection button a {
  color: #fff;
}

.getdirection .phone-container a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fb0;
  line-height: 25px;
  transition: all 0.5s;
  font-size: 1.8rem;
  text-shadow: 1px 1px 1px rgb(0 0 0 / 50%);
}

.getdirection .phone-container .phone {
  width: 35px;
  height: 35px;
  margin-right: 10px;
}

.myfoot {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.myfoot .socials {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.myfoot .socials a {
  display: block;
  width: 25px;
  height: 25px;
  margin-right: 10px;
  border-radius: 50%;
  box-shadow: 0 5px 15px rgb(0 0 0 / 20%);
  overflow: hidden;
}

.myfoot .socials a img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  filter: grayscale();
  transition: all 0.5s;
}

.myfoot .socials a:hover img {
  filter: none;
}

.copyright a {
  color: #fff;
}

/******************************************************************/
/************************ Admin Dashbord **************************/
/******************************************************************/

.delete-btn {
  border: none;
  padding: 0;
  margin: 0 0 0 10px;
  background: none;
  cursor: pointer;
  color: #a02020;
  transition: all 0.5s !important;
}

.delete-btn:hover {
  transform: scale(1.2);
}

.product-img-table {
  width: 80px;
  height: auto;
  object-fit: cover;
}

.icon-table {
  width: 50px;
  height: auto;
  object-fit: cover;
  background: #CCC;
}

.table-header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table-header-container button {
  border: none;
  padding: 0;
  background: none;
  cursor: pointer;
  margin-bottom: 40px;
}

.table-header-container svg {
  transition: all 0.5s;
}

.table-header-container svg:hover {
  transform: scale(1.15);
}

.admin-actions svg {
  transition: all 0.5s;
  margin-right: 5px;
  cursor: pointer;
}

.admin-actions svg:first-of-type {
  color: #20a020;
}

.admin-actions svg:last-of-type {
  color: #a02020;
}

.admin-actions svg:hover {
  transform: scale(1.2);
}

.admin-icon-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 200px;
  margin: 0 0 0 20px;
  border: 1px solid #eee;
  border-radius: 10px;
  overflow: hidden;
}

.add-image {
  width: 100px;
}

.image-display {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.input-upload {
  display: none;
}

.icon-upload {
  display: flex;
  align-items: center;
  margin-bottom: 40px !important;
}

.select-image {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
}

.admin-image-container {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid rgba(233, 229, 221, 0.479);
  margin: 50px auto 20px;
  box-shadow: 0 5px 20px rgb(0 0 0 / 10%);
  overflow: hidden;
}

.admin-image-container.slider {
  height: 280px;
}

.paper-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  padding-right: 20px;
}

.paper-title {
  font-size: 1.2rem;
